nav {
	display: flex;
	justify-content: space-between;
	padding-inline: 2rem;
	height: 9rem;
	position: relative;
}

#nav-left {
	position: relative;
	align-self: center;
}

#nav-right {
	display: flex;
	z-index: 1000;
	gap: 0.5em;
}

.nav-item {
	display: flex;
	position: relative;
	width: 60px;
	height: 6rem;
	margin-top: -1px;
	align-items: center;
	justify-content: center;
	transition: 0.25s ease;
	border: 1px solid transparent;
	border-bottom-left-radius: 30px;
	border-bottom-right-radius: 30px;
	background: var(--gray-100);
	background-clip: padding-box;
	user-select: none;
	scroll-behavior: smooth;
}

.nav-item::after {
	content: "";
	z-index: -1;
	position: absolute;
	top: -1px; bottom: -1px;
	left: -1px; right: -1px;
	background: var(--primary);
	border-bottom-left-radius: 30px;
	border-bottom-right-radius: 30px;
	box-shadow: 0 0 0.5em rgba(0, 0, 0, 0.5);
}

.nav-item:hover {
	height: 8rem;
	cursor: pointer;
}

.nav-item svg {
	fill: var(--gray-900);
}

.nav-item:hover::after {
	background: linear-gradient(45deg, var(--primary), var(--secondary));
	box-shadow: 0 0 0.25em var(--primary);
}

.nav-item:active {
	background: var(--gray-300);
	box-shadow: 0 0 0.5em var(--primary);
}

.nav-item:active::after {
	box-shadow: 0 0 0.5em var(--primary);
}

.menu-category-header {
	/* display: flex; */
	justify-content: space-between;
	align-items: center;
}

.menu-category-title {
	background: url("./assets/title-bg.png") no-repeat;
	filter: drop-shadow(0 0 0.1em var(--primary));
	text-shadow: 0 0 0.25em var(--gray-100);
	width: 400px;
	height: 48px;
	transition: 0.25s ease;
	user-select: none;
	padding-left: 24px;
	line-height: 48px;
	scroll-margin-top: 1em;
	margin-bottom: 0.5em;
}

.menu-category-nav {
	margin: 0;
	padding: 0;
	margin-bottom: 1em;
}

.menu-category-nav a {
	display: inline-block;
	margin-inline: 0.25em;
	margin-block: 0.25em;
	border-bottom: 1px solid var(--primary);
	border-top: 1px transparent;
	font-size: 11px;
	font-family: "Mazzard H Medium";
	color: var(--gray-900);
	text-decoration: none;
	list-style: none;
	transition: 0.25s ease;

	font-size: 13px;
	color: var(--gray-900);
	text-decoration: none;
	padding-inline: 1em;
	padding-block: 0.5em;
}

.menu-category-nav a:hover {
	border-radius: 0.25em;
	background-color: var(--primary);
}

.menu-group {
	scroll-margin-top: 1.25em;
	clear: both;
}

.menu-group-title {
	text-align: center;
	margin-bottom: 1em;
}

.menu-items {
	display: grid;
	gap: 0.5em;
	grid-template-columns: repeat(3, 1fr);
	margin-bottom: 1.5em;
	align-items: flex-start;
	
}

#content h1:not(:first-child) {
	margin-top: 2em;
}

article {
	/* display: flex;
	flex-direction: column;
	gap: 0.5em; */
	padding: 1em;
	background: radial-gradient(var(--gray-500), var(--gray-100));
	border: 1px solid var(--gray-500);
	border-radius: 1em;
	transition: 0.25s ease;
}

article > *:not(:last-child) {
	margin-bottom: 0.5em;
}

article.new-item { border-color: var(--label-new-border) }
article.popular-item { border-color: var(--label-popular-border) }
article.discount-item { border-color: var(--label-discount-border) }

article:hover {
	filter: drop-shadow(0 0 0.5em var(--primary));
	border-color: var(--primary);
}

article:hover > .image-container {
	height: 140px;
}

.item-info {
	display: flex;
	justify-content: space-between;
	padding-inline: 0.5em;
	margin-top: 0.5em;
}

.item-info span {
	display: flex;
	font-size: 0.9em;
	align-items: center;
	gap: 0.25em;
	margin-left: 0.25em;
}

.item-description {
	color: #999;
	font-size: 12px;
	margin: 0;
	margin-top: -0.5em;
}

.image-container {
	width: 100%;
	height: 128px;
	overflow: hidden;
	border: 1px solid var(--gray-500);
	border-radius: 0.5em;
	transition: height 0.25s ease;
	cursor: pointer;
	margin-bottom: 0.25em;
}

.image-container:active {
	filter: brightness(0.75);
}

.image-container img {
	width: 100%;
	height: 100%;
	aspect-ratio: 16 / 9;
	object-fit: cover;
}

.menu-tag {
	background-color: var(--gray-100);
	border: 1px solid var(--gray-500);
	border-radius: 0.75em;
	font-size: 11px;
	font-family: "Mazzard H Medium";
	color: var(--gray-700);
	padding: 3px 12px;
	user-select: none;
	cursor: pointer;
	transition: 0.25s ease;
}

.menu-tag:hover {
	background-color: var(--primary);
	border-color: var(--primary-border);
	color: #fff;
}

.tag-new {
	background-color: var(--label-new);
	border-color: var(--label-new-border);
	color: var(--gray-100);
}

.tag-popular {
	background-color: var(--label-popular);
	border-color: var(--label-popular-border);
	color: var(--gray-100);
}

.tag-discount {
	background-color: var(--label-discount);
	border-color: var(--label-discount-border);
	color: var(--gray-100);
}

.menu-item-allergen {
	display: flex;
	position: relative;
	gap: 0.25em;
	align-items: center;
}

.menu-item-allergen:hover > .allergen-information,
.allergen-information:hover > .allergen-information {
	display: grid;
}

.menu-item-allergen svg {
	width: 1em;
	height: 1em;
	margin: 0;
	padding: 0;
}

.allergen-information {
	display: none;
	align-items: center;
	gap: 0.25em 0.5em;
	grid-template-columns: repeat(2, auto);
	position: absolute;
	left: -0.5em;
	top: 1.75em;
	z-index: 1000;
	padding: 0.75em 1em;
	font-family: "Mazzard H Medium";
	text-transform: uppercase;
}

.allergen-information::before {
	content: "";
	position: absolute;
	width: 1em;
	height: 1em;
	top: -0.25em;
	left: 0.5em;
	transform: rotate(45deg);
	background: var(--gray-700);
	z-index: -1;
}

.allergen-information::after {
	content: "";
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background-color: var(--gray-100);
	border: 1px solid var(--gray-700);
	border-radius: 0.5em;
	z-index: -1;
}

#scroll-to-top {
	position: fixed;
	right: 2em;
	bottom: 2em;
	aspect-ratio: 1;
	background: var(--gray-100);
	border: 1px solid var(--primary);
	border-radius: 50%;
	cursor: pointer;
	transition: 0.25s ease;
}

#scroll-to-top svg {
	width: 2.5em;
	height: 2.5em;
	fill: var(--gray-900);
}

.hidden {
	opacity: 0;
	user-select: none;
	pointer-events: none;
}

@media only screen and (max-width: 1200px) {
	.menu-items {
		grid-template-columns: repeat(2, 1fr);
	}
}

@media only screen and (max-width: 1024px) {
	body {
		padding: 0 10%
	}

	#scroll-to-top svg {
		margin: 0.5em;
	}
}

@media only screen and (max-width: 800px) {
	nav {
		flex-direction: column;
		justify-content: center;
		height: auto;
		padding-inline: unset;
		padding-block: 1em;
		/* margin-inline: -20%; */
		align-items: center;
	}

	#nav-left {
		margin-block: 1em;
	}

	#nav-right {
		width: 100%;
	}

	.nav-item {
		flex: 1;
		width: auto;
		height: 2.5em;
		border-radius: 1em;
	}

	.nav-item:hover {
		height: auto;
	}

	.nav-item::after {
		border-radius: inherit;
	}

	.menu-category-title {
		background: none;
		filter: none;
		width: 80%;
		height: auto;
		margin: auto;
		margin-bottom: 0.5em;
		text-align: center;
		padding: 0;
		border-bottom: 1px solid var(--primary);
	}

	.menu-category-nav a {
		background-color: var(--primary);
		border-radius: 0.5em;
	}

	.menu-items {
		grid-template-columns: repeat(1, 1fr) !important;
	}
}